@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: 'Noto Sans TC', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}


.bg {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #ffffff;
}


.input-todo {
  margin: 10px 0;
  height: 2.5rem;
  width: 16rem;
  padding: 0 30px;
  border-radius: 20px;
  border: none; 
  box-shadow: 0px 5px 12px -10px #000000;
}

.input-todo:active{
  border: none;
}

.input-todo-edit{
  margin: 10px 0;
  height: 2.5rem;
  width: 16rem;
  padding: 0 30px;
  font-size: 1.1rem;
  border-radius: 20px;
  border: none;
  background-color: thistle; 
  box-shadow: 0px 5px 12px -10px #000000;
}

.edit-todo{
  margin-left: 2rem;
}


#add-btn {
  border: none;
  margin: 0 0.5rem;
  background-color: #38ada9;
  color: white;
  width: 2rem;
  height: 2rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  border-radius: 50%;
}

#add-btn:hover {
  background-color: #079992;
  transform: scale(1.1);
}

#update-btn {
  background: steelblue;
  border: none;
  margin: 0 0.5rem;
  padding: 0.3rem;
  color: white;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  border-radius: 5px;
}



h4{
    text-align: center;
}
.todo-task {
  margin: 10px 0;
  height: 2.5rem;
  width: 19.6rem;
  padding: 0 30px;
  border-radius: 20px;
  border: none;
  vertical-align: middle;
  box-shadow: 0px 5px 12px -10px #000000;
  background-color: white;
  color: #000000;
}


.todo-item {
  top: 0.4rem;
  font-size: 1.1rem;
  overflow: hidden;
  position: relative;
  cursor: pointer;
}

.complete {
  text-decoration: line-through;
}

.btn {
  float: right;
  position: relative;
  top: -1.2rem;
  right: -6.5rem;
}

#del-btn {
  cursor: pointer;
  fill: #e17055;
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 1rem;
}

#edit-btn {
  cursor: pointer;
  fill: #74b9ff;
  width: 1.5rem;
  height: 1.5rem;
}

.from-bottom {
  position: relative;
  -webkit-animation: animatebottom 0.4s;
          animation: animatebottom 0.4s
}

@-webkit-keyframes animatebottom {
  from {
    bottom: -15px;
    opacity: 0
  }

  to {
    bottom: 0;
    opacity: 1
  }
}

@keyframes animatebottom {
  from {
    bottom: -15px;
    opacity: 0
  }

  to {
    bottom: 0;
    opacity: 1
  }
}
